@keyframes example {
  from {
    transform: scale(0.9);
  }
  to {
    transform: scale(1);
  }
}

/* main header styles */
header {
  /* min-width: 490px; */
  position: sticky;
  top: 0;
  z-index: 999;
  background: #F9F9F9;
  width: 100%;
  margin: 0px auto 10px auto;
  padding: 10px 20px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  transition: 0.3s linear;
}

/* logo styles */
.logo {
  display: flex;
  align-items: center !important;
}

.logo img {
  width: 67px;
  height: 67px;
  margin-right: 5px;
}

.logo p{
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  color: #02307d;
}

/* header top styles */
.header__top, .header__bottom{
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.header__top__main, .header__top__icon{
  display: flex;
padding: 0;
margin: 0;
}

/* header other styles */
.header__bottom ~ .dropdown:hover .header__bottom .dropbtn{
  
  background: #cddcf6 !important;
  color: #02307d;
}
.header_border{
  border:1px solid blue;
  border-radius:5px;
}
.header__nav:hover, .dropbtn:hover, .header__bottom .dropdown-content .header__nav:hover{
  color:#2169e7;
 
}
.header__bottom .header__nav, .header__bottom .dropbtn{
  background: none;
}

.header__nav, .dropbtn{

  height: 40px;
font-weight: 600;
margin-right: 12px;
padding: 7px 13px;
text-decoration: none;
vertical-align: middle;
font-style: normal;
font-weight: 500;
font-size: 15px;
color: #02307d;
}

.submenu__item{
  margin: 0;
}

.header__right{
  display: flex;
flex-direction: column;
gap: 10px;
}
/* dropdown styles */
  .dropdown {
    position: relative;
    display: inline-block;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
  }
  
  .dropdown-content {
    position: absolute;
    z-index: 999;
    display: none;
    background: white;
    border-radius: 10px;
    box-shadow: 2px 2px 10px 2px #cddcf6;
    text-align: left;
    padding: 0px;
    transition: 0.3s linear;
  }

  .dropdown-content .header__nav {
    text-decoration: none;
    vertical-align: middle;
    text-transform: uppercase;
    font-style: normal;
    font-size: 15px;
    color: #02307d;
    /* border-radius: 10px; */
    width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: none;
  }

 

  .dropdown:hover .dropdown-content {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    transform: scale(1);
    animation-name: example;
    animation-duration: 0.5s;
    animation-fill-mode: backwards;
    z-index: 999;
   
    margin: -10px 0;
  }



  .dropbtn{
    padding: 11px 13px 7px 13px;
  }

  .institut__card{
    display: flex;
    flex-direction: column;
  }
  
  /* header icon styles  */
  .icon__wrapper .submenu__item{
    width: 60px !important;
    height: 30px;
    margin-left: 0;
  
    text-align: center;
  }

  .menu-icon{
    color: #02307d;
  vertical-align: middle;
  margin-left: 12px;
  transition: 0.3s ease-in-out;
  }

  .gerb__img{
    width: 25px;
    height: auto;
  }

/* document body change mode styles */
.greyscale {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
}

.burger, .close__header{
  display: none;
}

/* media queries  */
@media only screen and (max-width: 1390px){
  .header__nav, .dropbtn{
    padding: 4px 7px;
    height: 30px;
    margin-right: 7px;
    font-size: 13px;
  }
  .dropbtn{
    padding: 7px 7px 3.5px 7px;
  }
  .menu-icon{
    margin-left: 3px;
  }
  .gerb__img{
    width: 20px;
  }
  .dropdown-content .submenu__item{
    font-size: 13px;
    margin-right: 0;
  }
  .icon__wrapper .submenu__item{
    width: 45px !important;
  }
  .icon__wrapper{
    margin-right: 10px;
  }
  .header__bottom{
    margin-top: -10px;
  }
  .logo p {
    font-size: 12px;
  }
}

